.dropdown {
  opacity: 0.7;
  font-family: Arial;
  text-align: center;
  position: absolute;
  left: 20px;
  top: 40px;
}

.dropdown2 {
  border: 1px solid;
  opacity: 1;
  font-weight: bold;
  font-family: Arial;
  border-radius: 5px;
  min-height: 23px;
  min-width: 150px;
  font-weight: 800;
}
.dropdown2:hover {
  border-color: dodgerblue;
}

.dropdown:hover {
  opacity: 0.8;
}

